import "./processorInstructions.css"

function ProcessorInstructions ({ setCode, setInvalidLine }) {

    let sync = () => {
        setInvalidLine(-2); // this is to resync code in textarea
    };

    const helloWorld = `movr a 111
movr b 108
movr c 101
movr d 72
push a		// o
push b		// l
push b		// l
push c		// e
push d		// H
put
put
put
put
put
push 32		// mezera
put
push 100	// d
push 108	// l
push 114	// r
push 111	// o
push 119	// w
put
put
put
put
put
push 33		// !
put
push 32		// mezera
put
push 41		// )
push 58		// :
put
put
halt`;

    let cycle = `movr c 5	// pocet cyklu

// zacatek cyklu
push c		// vtlaci hodnotu reg C do zasobniku
dec c		// snizi reg C o jedna
cmp c 0		// porovnani
jz 10		// pokud Zero tak vyskoci ven z cyklu
loop 3		// cyklus

halt		// konec`;

    let tutorial = `// Inicializace zsobnku
PUSH 10      // Push prvn slo na zsobnk
PUSH 20      // Push druh slo na zsobnk

// Nast sla ze zsobnku do registr
POP B        // Registr B = 20
POP C        // Registr C = 10

// Stn sel
ADD B C      // Registr B = 20 + 10 = 30

// Rozdlen vsledku na 2
MOVR D 10       // Nastavit registr D na 10
PUSH 2       // Push slo 2 na zsobnk
POP D        // Registr D = 2

// Dlen
DIV B D      // Registr B = 30 / 2 = 15

// Prohozen hodnot registr
SWAP D B      // Registr D <=> B

// Zven hodnoty v registru D
INC D        // Registr D = 15 + 1 = 16

// Srovnn
CMP D 16     // Porovnat registr D (16) s slem 16
JZ 24       // Skok, pokud jsou stejn
LOOP 32     // preskoci HALT
HALT                    // Ukonit program

// Logick operace
MOVR A 5      // Nastavit registr A na 5
MOVR B 3      // Nastavit registr B na 3

AND A B      // Logick AND mezi registry A a B (101 & 011 = 001)
OR A B       // Logick OR mezi registry A a B (001 | 011 = 011)
XOR A B      // Logick XOR mezi registry A a B (011 ^ 011 = 000)

// Posun bit
PUSH 4       // Push slo 4 na zsobnk
POP D        // Registr D = 4
SHL D        // Posun vlevo (4 << 1 = 8)

// Skoit na konec programu
LOOP 30       // Skok na konec programu

HALT                    // Ukonit program`;


    return (
        <div className="container">
        <h1>vod</h1>                            {/* _______________uvod section__________________________ */}
        <div className="instruction">
            <h3>Syntax</h3>
            <pre>
{`instrukce argumenty            // koment`}
            </pre>
        </div>
        <div className="instruction">
            <h3>Formt sla</h3>           
            <pre>
{`destkov: 200
hexadecimln: 0xC8
binrn: 11001000`}
            </pre>
        </div>
        <h1>Instrukce</h1>                       {/* _______________instrukce section_________________________ */}
        <div className="instruction">
            <h3>Bez operace</h3>
            <pre>
{`NOP           // No Operation`}
            </pre>
            <p>Neudl nic</p>
        </div>
        <div className="instruction">
            <h3>Stn a odtn</h3>
            <pre>
{`ADD reg num           // Addition
ADD reg reg
SUB reg num           // Subtraction
SUB reg reg`}
            </pre>
            <p>Set nebo odete dv sla dohromady a vsledek ulo do prvnho registru</p>
        </div>
        <div className="instruction">
            <h3>Nsoben a dlen</h3>
            <pre>
{`MUL reg num           // Multiplication
MUL reg reg
DIV reg num           // Division
DIV reg reg`}
            </pre>
            <p>Vynsob nebo vydl dv sla dohromady a vsledek ulo do prvnho registru</p>
        </div>
        <div className="instruction">
            <h3>Inkrementace a dekrementace</h3>
            <pre>
{`INC reg           // Increase
DEC reg           // Decrease`}
            </pre>
            <p>Zv nebo sn hodnotu registru o 1</p>
        </div>
        <div className="instruction">
            <h3>Vloen hodnoty do registru</h3>
            <pre>
{`MOVR reg          // Move to registr`}
            </pre>
            <p>Vlo do registru konstantu zapsanou destkov</p>
        </div>
        <div className="instruction">
            <h3>Prce se zsobnkem</h3>
            <pre>
{`PUSH reg
PUSH num
POP reg`}
            </pre>
            <p>Vtla hodnotu registru nebo konstantu do zsobnku a nebo vybere ze zsobnku hodnotu a vlo ji do registru</p>
        </div>
        <div className="instruction">
            <h3>Prohozen</h3>
            <pre>
{`SWAP reg reg`}
            </pre>
            <p>Prohod hodnoty registr</p>
        </div>
        <div className="instruction">
            <h3>Prce se vstupem a vstupem</h3>
            <pre>
{`GET
PUT`}
            </pre>
            <p>Vezme znak ze vstupu a vlo ho na zsobnk nebo vyjme hodnotu ze zsobnku a vytiskne ji na vstup</p>
        </div>
        <div className="instruction">
            <h3>Porovnvn</h3>
            <pre>
{`CMP reg reg           // Compare
CMP reg num`}
            </pre>
            <p>Porovn dv hodnoty a nastav pznaky</p>
        </div>
        <div className="instruction">
            <h3>Logick instrukce</h3>
            <pre>
{`AND reg reg
AND reg num
OR reg reg
OR reg num
XOR reg reg
XOR reg num
NOT reg`}
            </pre>
            <p>Logick souin, logick souet, vlun disjunkce, negace</p>
        </div>
        <div className="instruction">
            <h3>Binrn posun</h3>
            <pre>
{`SHL reg         // Shift Left
SHR reg         // Shift Right`}
            </pre>
            <p>Posune hodnotu registru o 1 bit doleva nebo doprava</p>
        </div>
        <div className="instruction">
            <h3>Skoky</h3>
            <pre>
{`JZ num          // Jump if Zero
JNZ num         // Jump if Not Zero
JS num          // Jump if Sign
JNS num         // Jump if Not Sign`}
            </pre>
            <p>Pokud je splnna podmnka tak sko na slo dku</p>
        </div>
        <div className="instruction">
            <h3>Cyklus</h3>
            <pre>
{`LOOP address`}
            </pre>
            <p>Sko na slo dku</p>
        </div>
        <div className="instruction">
            <h3>Zastaven procesoru</h3>
            <pre>
{`HALT`}
            </pre>
            <p>Zastav procesor</p>
        </div>
        <div className="instruction">            {/* _______________programy section__________________________ */}
            <h1>Program Hello world!
            <button 
                className="setCode"
                onClick={() => {
                window.scrollTo({ top: 0, behavior: 'smooth' });
                setCode(helloWorld);
                sync();
            }}>SPUSTIT!</button></h1>
            <pre>{helloWorld}</pre>
        </div>
        <div className="instruction">
            <h1>Program Cyklus 
            <button 
                className="setCode"
                onClick={() => {
                window.scrollTo({ top: 0, behavior: 'smooth' });
                setCode(cycle);
                sync();
            }}>SPUSTIT!</button></h1>
            <pre>{cycle}</pre>
        </div>
        <div className="instruction">
            <h1>Program Tutorial 
            <button 
                className="setCode"
                onClick={() => {
                window.scrollTo({ top: 0, behavior: 'smooth' });
                setCode(tutorial);
                sync();
            }}>SPUSTIT!</button></h1>
            <pre>{tutorial}</pre>
        </div>

        <p>Vytvoil: Dan Tabarka</p>
        <p>Odkaz na <a  href="https://github.com/DanTabarka/AssemblerSimulator"
                        target="_blank"
                        rel="noopener noreferrer"
                    >GitHub</a>
        </p>
        
    </div>
    )

}

export default ProcessorInstructions;
